#logos-top {
    width: 70%;
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
  margin: 0 auto;
}

#logos-top > img {
  width: 2rem;
  height: 100%;
  max-height: 100%;
  object-fit: contain;
}

@media (max-width: 768px) {
    #logos-top > img {
      width: 1.4rem; 
    }
  }